import React, { useState, useEffect, useRef } from 'react';
import ReactDOM from 'react-dom/client';
// Import data promise - will load complete processed data file
import { covidDataPromise } from './covidData';
import { WorldMap } from './map';
import { LineChart } from './lineChart';

// Shared country name mappings
const COUNTRY_NAME_MAPPINGS = {
  // Map display mappings (topojson to dataset)
  "United States of America": "United States",
  "USA": "United States",
  "Democratic Republic of the Congo": "Democratic Republic of Congo",
  "Republic of the Congo": "Congo",
  "Dem. Rep. Congo": "Democratic Republic of Congo",
  "Czech Republic": "Czechia",
  "Republic of Serbia": "Serbia",
  "Tanzania": "United Republic of Tanzania",
  "Guinea Bissau": "Guinea-Bissau",
  "Côte d'Ivoire": "Cote d'Ivoire"
};

// Alternative names for search
const ALTERNATIVE_COUNTRY_NAMES = {
  "United States": ["United States of America"],
  "United States of America": ["United States"],
  "United Kingdom": ["UK", "Britain", "Great Britain", "U.K."],
  "Democratic Republic of Congo": ["DRC", "Congo-Kinshasa"],
  "Republic of Congo": ["Congo-Brazzaville"],
  "South Korea": ["Korea, South", "Korea Republic"],
  "North Korea": ["Korea, North", "Democratic People's Republic of Korea"],
  "Russia": ["Russian Federation"],
  "Taiwan": ["Taiwan*", "Chinese Taipei"]
};

function App() {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedCountry, setSelectedCountry] = useState('World');
  const [countries, setCountries] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const mapContainerRef = useRef(null);
  const [map, setMap] = useState(null);
  const [selectedMetric, setSelectedMetric] = useState('total_cases');
  const trendChartRef = useRef(null);
  const [lineChart, setLineChart] = useState(null);
  const [selectedTrendMetric, setSelectedTrendMetric] = useState('total_cases');
  const [showChatMessage, setShowChatMessage] = useState(false);

  useEffect(() => {
    // Load COVID data
    const loadData = async () => {
      try {
        const fullData = await covidDataPromise;
        
        if (!fullData || Object.keys(fullData).length === 0) {
          throw new Error('Load data is empty');
        }
        
        setData(fullData);
        
        // Get country list and sort alphabetically
        const countryList = Object.keys(fullData).sort();
        setCountries(countryList);
        
        // If there is World data, default select, otherwise select the first country
        if (countryList.includes('World')) {
          setSelectedCountry('World');
        } else if (countryList.length > 0) {
          setSelectedCountry(countryList[0]);
        }
        
        setLoading(false);
      } catch (error) {
        console.error('Error loading data:', error);
        setError(error.message || 'Error loading data');
        setLoading(false);
      }
    };

    loadData();
  }, []);

  // Initialize map after data is loaded
  useEffect(() => {
    if (data && mapContainerRef.current && !map) {
      // Create a data adapter for the map
      const mapDataAdapter = {
        getCountryData: (countryName) => {
          // Use the shared mapping
          const dataName = COUNTRY_NAME_MAPPINGS[countryName] || countryName;
          return data[dataName] || null;
        },
        getSelectedMetric: () => selectedMetric,
        getMetricLabel: (metric) => {
          const labels = {
            'total_cases': 'Total Cases',
            'total_deaths': 'Total Deaths',
            'new_cases': 'New Cases',
            'new_deaths': 'New Deaths'
          };
          return labels[metric] || metric;
        },
        getSelectedCountry: () => selectedCountry,
        setSelectedCountry: (country) => setSelectedCountry(country),
        getMaxValue: () => {
          let max = 0;
          Object.values(data).forEach(countryData => {
            if (countryData && countryData.length > 0) {
              const latest = countryData[countryData.length - 1];
              if (latest[selectedMetric] > max) max = latest[selectedMetric];
            }
          });
          return max;
        }
      };

      const worldMap = new WorldMap(mapContainerRef.current, mapDataAdapter);
      worldMap.init();
      setMap(worldMap);

      // Handle window resize
      const handleResize = () => {
        if (worldMap) worldMap.resize();
        if (lineChart) lineChart.resize();
      };
      window.addEventListener('resize', handleResize);
      return () => window.removeEventListener('resize', handleResize);
    }
  }, [data, mapContainerRef, map, selectedCountry, selectedMetric]);
  
  // Update map when metric changes
  useEffect(() => {
    if (map) {
      // Force update the map's data adapter reference to the current metric
      // This ensures the map knows about the metric change
      const updatedAdapter = {
        ...map.data,
        getSelectedMetric: () => selectedMetric
      };
      
      // Update the map's data reference
      map.data = updatedAdapter;
      
      // Now update the map with the new metric
      map.updateMap();
    }
  }, [selectedMetric, map]);

  // Update map when country selection changes
  useEffect(() => {
    if (map) {
      map.updateMap();
    }
  }, [selectedCountry, map]);

  // Normalize selected country names
  useEffect(() => {
    // Convert "United States of America" to "United States" if selected
    if (selectedCountry === "United States of America") {
      setSelectedCountry("United States");
    }
  }, [selectedCountry]);

  // Initialize and update line chart when data or selected country changes
  useEffect(() => {
    if (data && trendChartRef.current && selectedCountry) {
      const countryData = data[selectedCountry];
      
      if (!countryData) return;

      const metricLabels = {
        'total_cases': 'Total Cases',
        'new_cases': 'New Cases',
        'total_deaths': 'Total Deaths',
        'new_deaths': 'New Deaths'
      };
      
      if (!lineChart) {
        // Create new chart
        const chart = new LineChart(trendChartRef.current, countryData, {
          height: 350,
          metric: selectedTrendMetric,
          metricLabel: metricLabels[selectedTrendMetric] || selectedTrendMetric
        });
        chart.init();
        setLineChart(chart);
      } else {
        // Update existing chart
        lineChart.updateData(
          countryData, 
          selectedTrendMetric, 
          metricLabels[selectedTrendMetric] || selectedTrendMetric
        );
      }
    }
  }, [data, selectedCountry, selectedTrendMetric, lineChart]);

  // Filter country list
  const filteredCountries = countries.filter(country => {
    // Create a case-insensitive search
    const searchLower = searchTerm.toLowerCase();
    
    // Direct match with country name
    if (country.toLowerCase().includes(searchLower)) return true;
    
    // Check if the current country has alternative names that match the search
    for (const [fullName, altNames] of Object.entries(ALTERNATIVE_COUNTRY_NAMES)) {
      if (country === fullName && altNames.some(alt => alt.toLowerCase().includes(searchLower))) {
        return true;
      }
    }
    
    // Check for the reverse - if searching for a full name but the dataset uses an alternative
    for (const [fullName, altNames] of Object.entries(ALTERNATIVE_COUNTRY_NAMES)) {
      if (altNames.includes(country) && fullName.toLowerCase().includes(searchLower)) {
        return true;
      }
    }
    
    return false;
  });

  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading">Loading COVID-19 data...</div>
        <p>Please wait, we are loading complete data. This may take some time.</p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="error-container">
        <div className="error">Error loading data: {error}</div>
        <p>There was a problem loading COVID-19 data. Please refresh the page or check the browser console for detailed error information.</p>
      </div>
    );
  }

  if (!data) {
    return (
      <div className="error-container">
        <div className="error">No data available. Please check the browser console for error information.</div>
        <p>There was a problem loading COVID-19 data. Please refresh the page.</p>
      </div>
    );
  }

  // Add data display
  const renderData = () => {
    if (!data[selectedCountry] || data[selectedCountry].length === 0) {
      return <p>No data available for {selectedCountry}</p>;
    }

    // Get the latest data record for the country
    const latestData = data[selectedCountry][data[selectedCountry].length - 1];
    // Get previous day's data to calculate change
    const previousData = data[selectedCountry].length > 1 
      ? data[selectedCountry][data[selectedCountry].length - 2] 
      : null;
    
    // Calculate change percentage
    const calculateChange = (current, previous, field) => {
      if (!previous || !previous[field] || previous[field] === 0) return null;
      const change = ((current[field] - previous[field]) / previous[field]) * 100;
      return change.toFixed(2);
    };

    const casesChange = calculateChange(latestData, previousData, 'total_cases');
    const deathsChange = calculateChange(latestData, previousData, 'total_deaths');

    return (
      <div className="data-display">
        <h3>Latest Data for {selectedCountry} on {latestData.date}</h3>
        
        <div className="data-grid">
          <div className="data-card">
            <h4>Total Cases</h4>
            <p className="data-value">{latestData.total_cases?.toLocaleString() || 'N/A'}</p>
            {casesChange && (
              <p className={`data-change ${parseFloat(casesChange) >= 0 ? 'increase' : 'decrease'}`}>
                {parseFloat(casesChange) >= 0 ? '↑' : '↓'} {Math.abs(parseFloat(casesChange))}%
              </p>
            )}
          </div>
          
          <div className="data-card">
            <h4>New Cases</h4>
            <p className="data-value">{latestData.new_cases?.toLocaleString() || 'N/A'}</p>
          </div>
          
          <div className="data-card">
            <h4>Total Deaths</h4>
            <p className="data-value">{latestData.total_deaths?.toLocaleString() || 'N/A'}</p>
            {deathsChange && (
              <p className={`data-change ${parseFloat(deathsChange) >= 0 ? 'increase' : 'decrease'}`}>
                {parseFloat(deathsChange) >= 0 ? '↑' : '↓'} {Math.abs(parseFloat(deathsChange))}%
              </p>
            )}
          </div>
          
          <div className="data-card">
            <h4>New Deaths</h4>
            <p className="data-value">{latestData.new_deaths?.toLocaleString() || 'N/A'}</p>
          </div>
        </div>
        
        {latestData.total_vaccinations && (
          <div className="vaccination-info">
            <h4>Vaccination Information</h4>
            <p>Total Vaccinations: {latestData.total_vaccinations?.toLocaleString() || 'N/A'}</p>
            <p>New Vaccinations: {latestData.new_vaccinations?.toLocaleString() || 'N/A'}</p>
          </div>
        )}
        
        {latestData.stringency_index && (
          <div className="stringency-info">
            <h4>Stringency Index: {latestData.stringency_index}</h4>
            <p>The stringency index measures the strictness of government policies.</p>
          </div>
        )}
      </div>
    );
  };

  // Render data trend
  const renderTrend = () => {
    if (!data[selectedCountry] || data[selectedCountry].length < 2) {
      return <p>Not enough data available for trend visualization</p>;
    }

    return (
      <div className="trend-container">
        <h3>COVID-19 Trend for {selectedCountry}</h3>
        <div className="trend-controls">
          <label htmlFor="trend-metric-select">Show: </label>
          <select 
            id="trend-metric-select"
            value={selectedTrendMetric}
            onChange={(e) => setSelectedTrendMetric(e.target.value)}
            className="metric-select"
          >
            <option value="total_cases">Total Cases</option>
            <option value="new_cases">New Cases</option>
            <option value="total_deaths">Total Deaths</option>
            <option value="new_deaths">New Deaths</option>
          </select>
        </div>
        <div ref={trendChartRef} className="trend-chart"></div>
      </div>
    );
  };

  return (
    <div className="app-container">
      <header>
        <h1>COVID-19 Data Dashboard</h1>
        <div className="country-selector">
          <input
            type="text"
            placeholder="Search countries..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
          <select 
            value={selectedCountry}
            onChange={(e) => {
              let country = e.target.value;
              // Convert specific country names for consistency
              if (country === "United States of America") {
                country = "United States";
              }
              setSelectedCountry(country);
            }}
            className="country-select"
          >
            {filteredCountries.map(country => (
              <option key={country} value={country}>
                {country}
              </option>
            ))}
          </select>
        </div>
      </header>

      <main>
        <div className="dashboard">
          <div className="card map-card">
            <h2>COVID-19 Map</h2>
            <div className="map-controls">
              <label htmlFor="metric-select">Color map by: </label>
              <select 
                id="metric-select"
                value={selectedMetric}
                onChange={(e) => {
                  setSelectedMetric(e.target.value);
                }}
                className="metric-select"
              >
                <option value="total_cases">Total Cases</option>
                <option value="new_cases">New Cases</option>
                <option value="total_deaths">Total Deaths</option>
                <option value="new_deaths">New Deaths</option>
              </select>
            </div>
            <div ref={mapContainerRef} className="map-container"></div>
          </div>
          <div className="card main-data-card">
            <h2>COVID-19 Data for {selectedCountry}</h2>
            {renderData()}
          </div>
          <div className="card trend-card">
            {renderTrend()}
          </div>
        </div>
      </main>
      
      {/* Floating Chat Agent */}
      <div className="chat-agent">
        <div 
          className="chat-bubble"
          onClick={() => setShowChatMessage(!showChatMessage)}
        >
          💬
        </div>
        {showChatMessage && (
          <div className="chat-message">
            Click on a country to see more data!
          </div>
        )}
      </div>
      
      <footer>
        <p>Data source: Our World in Data COVID-19 dataset</p>
        <p>Last updated: {data[selectedCountry]?.[data[selectedCountry].length - 1]?.date || 'Unknown'}</p>
      </footer>
    </div>
  );
}

const rootElement = document.getElementById('app');
if (rootElement) {
  ReactDOM.createRoot(rootElement).render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
}