import * as d3 from "d3";

export class LineChart {
    constructor(container, data, options = {}) {
        this.container = container;
        this.data = data;
        this.width = container.clientWidth;
        this.height = container.clientHeight || options.height || 300;
        this.margin = options.margin || { top: 35, right: 45, bottom: 65, left: 80 };
        this.metric = options.metric || 'total_cases';
        this.metricLabel = options.metricLabel || 'Total Cases';
        this.svg = null;
        this.tooltip = null;
        
        // Check date format and set up appropriate parser
        if (data && data.length > 0) {
            console.log("Sample date:", data[0].date);
            
            // Check if date is in YYYY-MM format (monthly data)
            if (data[0].date && data[0].date.match(/^\d{4}-\d{2}$/)) {
                console.log("Detected YYYY-MM format (monthly data)");
                this.parseDate = (dateStr) => {
                    // Convert YYYY-MM to YYYY-MM-15 for middle of month representation
                    return d3.timeParse("%Y-%m-%d")(dateStr + "-15");
                };
            } else if (data[0].date && data[0].date.endsWith("-15")) {
                console.log("Detected YYYY-MM-15 format (monthly data with day)");
                this.parseDate = d3.timeParse("%Y-%m-%d");
            } else {
                console.log("Detected full date format");
                this.parseDate = d3.timeParse("%Y-%m-%d");
            }
        } else {
            // Default parser
            this.parseDate = d3.timeParse("%Y-%m-%d");
        }
    }

    init() {
        this.createSVG();
        this.createTooltip();
        this.render();
    }

    createSVG() {
        // Clear any existing SVG
        d3.select(this.container).select("svg").remove();
        
        this.svg = d3.select(this.container)
            .append('svg')
            .attr('width', this.width)
            .attr('height', this.height)
            .attr('viewBox', `0 0 ${this.width} ${this.height}`)
            .attr('preserveAspectRatio', 'xMidYMid meet')
            .style('max-width', '100%')
            .style('height', 'auto')
            .attr('class', 'line-chart');
    }

    createTooltip() {
        // Remove existing tooltip if any
        d3.select('body').select('.chart-tooltip').remove();
        
        this.tooltip = d3.select('body')
            .append('div')
            .attr('class', 'chart-tooltip')
            .style('position', 'absolute')
            .style('visibility', 'hidden')
            .style('background-color', 'rgba(17, 34, 64, 0.9)')
            .style('color', '#e6f1ff')
            .style('padding', '8px 12px')
            .style('border-radius', '4px')
            .style('border', '1px solid #233554')
            .style('font-size', '14px')
            .style('pointer-events', 'none')
            .style('z-index', '1000');
    }

    render() {
        if (!this.data || this.data.length === 0) {
            this.renderNoData();
            return;
        }

        // Ensure minimum dimensions and adjust margins if necessary
        const minWidth = this.margin.left + this.margin.right + 100;
        const minHeight = this.margin.top + this.margin.bottom + 100;
        
        if (this.width < minWidth) {
            this.margin.left = Math.max(40, this.margin.left * 0.7);
            this.margin.right = Math.max(20, this.margin.right * 0.7);
        }
        
        if (this.height < minHeight) {
            this.margin.top = Math.max(20, this.margin.top * 0.7);
            this.margin.bottom = Math.max(40, this.margin.bottom * 0.7);
        }

        // Process data and parse dates
        const chartData = this.data.map(d => ({
            ...d,
            parsedDate: this.parseDate(d.date)
        })).sort((a, b) => a.parsedDate - b.parsedDate);
        
        console.log("Line chart data:", chartData.map(d => ({ date: d.date, parsedDate: d.parsedDate, value: d[this.metric] })));
        
        // Create scales
        const xScale = d3.scaleTime()
            .domain(d3.extent(chartData, d => d.parsedDate))
            .range([this.margin.left, this.width - this.margin.right]);
        
        const yScale = d3.scaleLinear()
            .domain([0, d3.max(chartData, d => d[this.metric] || 0) * 1.1]) // Add 10% padding
            .range([this.height - this.margin.bottom, this.margin.top]);
        
        // Create axes
        const xAxis = d3.axisBottom(xScale)
            .ticks(Math.min(chartData.length, 10)) // Show at most 10 ticks
            .tickFormat(d => {
                if (chartData.length > 36) {
                    return d3.timeFormat('%Y')(d);
                } else if (chartData.length > 12) {
                    return d3.timeFormat('%b %Y')(d);
                } else {
                    return d3.timeFormat('%b %Y')(d);
                }
            });
        
        const yAxis = d3.axisLeft(yScale)
            .ticks(5)
            .tickFormat(d => this.formatNumber(d));
        
        // Draw axes
        this.svg.append('g')
            .attr('class', 'x-axis')
            .attr('transform', `translate(0, ${this.height - this.margin.bottom})`)
            .call(xAxis)
            .selectAll('text')
            .style('text-anchor', 'end')
            .attr('dx', '-.8em')
            .attr('dy', '.15em')
            .attr('transform', 'rotate(-45)');
        
        this.svg.append('g')
            .attr('class', 'y-axis')
            .attr('transform', `translate(${this.margin.left}, 0)`)
            .call(yAxis);
        
        // Create a line generator
        const line = d3.line()
            .x(d => xScale(d.parsedDate))
            .y(d => yScale(d[this.metric] || 0))
            .curve(d3.curveMonotoneX); // Smooth curve
        
        // Draw line
        this.svg.append('path')
            .datum(chartData)
            .attr('fill', 'none')
            .attr('stroke', '#4facfe')
            .attr('stroke-width', 2)
            .attr('class', 'line')
            .attr('d', line);
        
        // Add data points with tooltips
        const circles = this.svg.selectAll('.data-point')
            .data(chartData)
            .enter()
            .append('circle')
            .attr('class', 'data-point')
            .attr('cx', d => xScale(d.parsedDate))
            .attr('cy', d => yScale(d[this.metric] || 0))
            .attr('r', 4)
            .attr('fill', '#4facfe')
            .style('opacity', 0);
        
        // Add transparent larger circles for better hover experience
        this.svg.selectAll('.hover-point')
            .data(chartData)
            .enter()
            .append('circle')
            .attr('class', 'hover-point')
            .attr('cx', d => xScale(d.parsedDate))
            .attr('cy', d => yScale(d[this.metric] || 0))
            .attr('r', 10)
            .attr('fill', 'transparent')
            .on('mouseover', (event, d) => {
                this.tooltip
                    .style('visibility', 'visible')
                    .html(`
                        <strong>${d3.timeFormat('%B %Y')(d.parsedDate)}</strong><br/>
                        ${this.metricLabel}: ${this.formatNumber(d[this.metric] || 0)}
                    `)
                    .style('left', (event.pageX + 10) + 'px')
                    .style('top', (event.pageY - 30) + 'px');
                
                // Highlight the point
                d3.select(circles.nodes()[chartData.indexOf(d)])
                    .style('opacity', 1)
                    .attr('r', 6);
            })
            .on('mouseout', (event, d) => {
                this.tooltip.style('visibility', 'hidden');
                
                // Restore the point
                d3.select(circles.nodes()[chartData.indexOf(d)])
                    .style('opacity', 0)
                    .attr('r', 4);
            });
        
        // Add chart title
        this.svg.append('text')
            .attr('x', this.width / 2)
            .attr('y', this.margin.top / 2)
            .attr('text-anchor', 'middle')
            .style('font-size', '16px')
            .style('font-weight', 'bold')
            .text(`${this.metricLabel} Over Time`);
    }

    renderNoData() {
        // Clear SVG
        this.svg.selectAll('*').remove();
        
        // Display no data message
        this.svg.append('text')
            .attr('x', this.width / 2)
            .attr('y', this.height / 2)
            .attr('text-anchor', 'middle')
            .style('font-size', '16px')
            .text('No data available');
    }
    
    updateData(data, metric, metricLabel) {
        this.data = data;
        if (metric) this.metric = metric;
        if (metricLabel) this.metricLabel = metricLabel;
        
        // Clear and redraw
        this.svg.selectAll('*').remove();
        this.render();
    }

    formatNumber(num) {
        if (num >= 1000000) {
            return (num / 1000000).toFixed(1) + 'M';
        } else if (num >= 1000) {
            return (num / 1000).toFixed(1) + 'K';
        } else {
            return num.toLocaleString();
        }
    }

    resize() {
        this.width = this.container.clientWidth;
        this.height = this.container.clientHeight || 300;
        this.svg.attr('width', this.width);
        this.svg.attr('height', this.height);
        // Clear and re-render to ensure proper sizing
        this.svg.selectAll('*').remove();
        this.render();
    }
} 