// World map visualization
import * as d3 from "d3";
import * as topojson from "topojson-client";
export class WorldMap {
    constructor(container, data) {
        this.container = container;
        this.data = data;
        this.width = container.clientWidth;
        this.height = 500;
        this.projection = d3.geoMercator()
            .scale(this.width / 2 / Math.PI)
            .translate([this.width / 2, this.height / 2]);
        this.path = d3.geoPath().projection(this.projection);
        this.svg = null;
        this.tooltip = null;
    }

    init() {
        this.createSVG();
        this.createTooltip();
        this.loadWorldData();
    }

    createSVG() {
        this.svg = d3.select(this.container)
            .append('svg')
            .attr('width', this.width)
            .attr('height', this.height)
            .attr('class', 'world-map');
    }

    createTooltip() {
        this.tooltip = d3.select('body')
            .append('div')
            .attr('class', 'tooltip')
            .style('position', 'absolute')
            .style('visibility', 'hidden')
            .style('background-color', 'rgba(17, 34, 64, 0.9)')
            .style('color', '#e6f1ff')
            .style('padding', '10px')
            .style('border-radius', '4px')
            .style('border', '1px solid #233554')
            .style('font-size', '14px')
            .style('z-index', '1000');
    }

    async loadWorldData() {
        try {
            const worldData = await d3.json('https://cdn.jsdelivr.net/npm/world-atlas@2/countries-110m.json');
            this.renderMap(worldData);
        } catch (error) {
            console.error('Error loading world data:', error);
        }
    }

    renderMap(worldData) {
        const countries = topojson.feature(worldData, worldData.objects.countries);
        const metric = this.data.getSelectedMetric();

        // Debug: Check what country names we have and which have data
        console.log("=== MAP DEBUG ===");
        console.log("Total countries in topojson:", countries.features.length);
        
        let countriesWithData = 0;
        let countriesWithoutData = [];
        
        countries.features.forEach(d => {
            const countryData = this.data.getCountryData(d.properties.name);
            if (countryData) {
                countriesWithData++;
            } else {
                countriesWithoutData.push(d.properties.name);
            }
        });
        
        console.log("Countries with data:", countriesWithData);
        console.log("Countries without data:", countriesWithoutData.length);
        console.log("First 10 countries without data:", countriesWithoutData.slice(0, 10));

        this.svg.selectAll('path')
            .data(countries.features)
            .enter()
            .append('path')
            .attr('d', this.path)
            .attr('fill', d => {
                const countryData = this.data.getCountryData(d.properties.name);
                if (!countryData) return '#112240';

                const latestData = countryData[countryData.length - 1];
                const value = latestData[metric];
                
                if (d.properties.name === this.data.getSelectedCountry()) {
                    return '#64ffda';
                }
                
                return this.getColorForValue(value);
            })
            .attr('stroke', '#233554')
            .attr('stroke-width', 0.5)
            .on('mouseover', this.handleMouseOver.bind(this))
            .on('mouseout', this.handleMouseOut.bind(this))
            .on('click', this.handleClick.bind(this));

        // Add a legend
        this.createLegend();
    }

    handleMouseOver(event, d) {
        const countryData = this.data.getCountryData(d.properties.name);
        if (!countryData) return;

        const latestData = countryData[countryData.length - 1];
        const metric = this.data.getSelectedMetric();
        const value = latestData[metric];

        this.tooltip
            .style('visibility', 'visible')
            .html(`
                <strong>${d.properties.name}</strong><br/>
                ${this.data.getMetricLabel(metric)}: ${this.formatNumber(value)}
            `)
            .style('left', (event.pageX + 10) + 'px')
            .style('top', (event.pageY - 10) + 'px');

        d3.select(event.currentTarget)
            .attr('fill', '#1d3461')
            .attr('stroke', '#64ffda')
            .attr('stroke-width', 1);
    }

    handleMouseOut(event, d) {
        this.tooltip.style('visibility', 'hidden');
        
        // Get the appropriate fill color based on the current metric
        const countryData = this.data.getCountryData(d.properties.name);
        const metric = this.data.getSelectedMetric();
        const selectedCountry = this.data.getSelectedCountry();
        
        let fillColor = '#112240'; // Default for countries with no data
        
        if (countryData && countryData.length > 0) {
            const latestData = countryData[countryData.length - 1];
            const value = latestData[metric];
            
            if (d.properties.name === selectedCountry) {
                fillColor = '#64ffda'; // Selected country color
            } else {
                fillColor = this.getColorForValue(value);
            }
        }
        
        d3.select(event.currentTarget)
            .attr('fill', fillColor)
            .attr('stroke', '#233554')
            .attr('stroke-width', 0.5);
    }

    handleClick(event, d) {
        let countryName = d.properties.name;
        
        // Apply special case handling for United States
        if (countryName === "United States of America") {
            countryName = "United States";
        }
        
        this.data.setSelectedCountry(countryName);
        this.updateMap();
    }

    updateMap() {
        const selectedCountry = this.data.getSelectedCountry();
        const metric = this.data.getSelectedMetric();
        
        this.svg.selectAll('path')
            .transition()
            .duration(300)
            .attr('fill', d => {
                const countryData = this.data.getCountryData(d.properties.name);
                if (!countryData || countryData.length === 0) return '#112240';

                const latestData = countryData[countryData.length - 1];
                const value = latestData[metric] || 0;
                
                // Handle country name mapping for selected country highlighting
                let mappedCountryName = d.properties.name;
                if (mappedCountryName === "United States of America") {
                    mappedCountryName = "United States";
                }
                
                if (mappedCountryName === selectedCountry) {
                    return '#64ffda'; // Highlight selected country
                }

                return this.getColorForValue(value);
            });
            
        // Update the legend with new metric
        this.createLegend();
    }

    createLegend() {
        // Remove existing legend if any
        this.svg.select(".legend-group").remove();
        
        const legendWidth = 200;
        const legendHeight = 20;
        const margin = { top: 20, right: 20, bottom: 20, left: 20 };
        
        // Create legend group
        const legendGroup = this.svg.append("g")
            .attr("class", "legend-group")
            .attr("transform", `translate(${this.width - legendWidth - margin.right}, ${margin.top})`);
            
        // Create gradient
        const gradient = legendGroup.append("defs")
            .append("linearGradient")
            .attr("id", "legend-gradient")
            .attr("x1", "0%")
            .attr("y1", "0%")
            .attr("x2", "100%")
            .attr("y2", "0%");
            
        // Get the max value for the current metric
        const maxValue = this.data.getMaxValue();
        
        // Add color stops to gradient
        const colorPoints = 10;
        for (let i = 0; i <= colorPoints; i++) {
            const offset = `${i * (100 / colorPoints)}%`;
            const value = (i / colorPoints) * maxValue;
            gradient.append("stop")
                .attr("offset", offset)
                .attr("stop-color", this.getColorForValue(value));
        }
        
        // Create rectangle with gradient
        legendGroup.append("rect")
            .attr("width", legendWidth)
            .attr("height", legendHeight)
            .style("fill", "url(#legend-gradient)");
            
        // Add labels
        legendGroup.append("text")
            .attr("x", 0)
            .attr("y", legendHeight + 15)
            .attr("text-anchor", "start")
            .style("font-size", "12px")
            .style("fill", "#e6f1ff")
            .text("0");
            
        legendGroup.append("text")
            .attr("x", legendWidth)
            .attr("y", legendHeight + 15)
            .attr("text-anchor", "end")
            .style("font-size", "12px")
            .style("fill", "#e6f1ff")
            .text(this.formatNumber(maxValue));
            
        // Add title
        legendGroup.append("text")
            .attr("x", legendWidth / 2)
            .attr("y", -5)
            .attr("text-anchor", "middle")
            .style("font-size", "12px")
            .style("fill", "#e6f1ff")
            .text(this.data.getMetricLabel(this.data.getSelectedMetric()));
    }

    getColorForValue(value) {
        if (!value || value === 0) return '#112240';
        
        // Get current metric and max value from data adapter
        const metric = this.data.getSelectedMetric();
        const maxValue = this.data.getMaxValue();
        
        // If max value is 0 or undefined, return default color
        if (!maxValue || maxValue === 0) return '#112240';
        
        // Normalize the value (0 to 1)
        const normalizedValue = Math.min(value / maxValue, 1);
        
        // Create color scale based on metric type
        let colorScale;
        if (metric.includes('deaths')) {
            // Red scale for deaths (more deaths = darker red)
            colorScale = d3.scaleSequential()
                .domain([0, 1])
                .interpolator(d3.interpolateReds);
        } else {
            // Blue scale for cases (more cases = darker blue)
            colorScale = d3.scaleSequential()
                .domain([0, 1])
                .interpolator(d3.interpolateBlues);
        }
        
        return colorScale(normalizedValue);
    }

    formatNumber(num) {
        return new Intl.NumberFormat().format(num);
    }

    resize() {
        this.width = this.container.clientWidth;
        this.projection
            .scale(this.width / 2 / Math.PI)
            .translate([this.width / 2, this.height / 2]);
        this.path = d3.geoPath().projection(this.projection);
        
        this.svg
            .attr('width', this.width)
            .attr('height', this.height);

        this.updateMap();
    }
} 