// Updated COVID data loader for preprocessed data

// Define the async function to load preprocessed data
async function loadCovidData() {
  try {
    console.log('Loading preprocessed COVID data...');
    
    // Load the preprocessed filtered data
    const response = await fetch('/filtered_covid_data.json');
    if (!response.ok) {
      throw new Error(`Unable to load preprocessed data: ${response.status}`);
    }
    const data = await response.json();
    
    console.log('Successfully loaded preprocessed COVID data');
    console.log(`- ${Object.keys(data).length} locations`);
    
    // Check data structure
    const sampleLocation = Object.keys(data)[0];
    const sampleData = data[sampleLocation];
    if (sampleData && sampleData.length > 0) {
      const firstRecord = sampleData[0];
      const lastRecord = sampleData[sampleData.length - 1];
      console.log(`- Date range: ${firstRecord.date} to ${lastRecord.date}`);
      console.log(`- Records per location: ${sampleData.length}`);
      console.log(`- Data format: YYYY-MM (preprocessed)`);
    }
    
    return data;
  } catch (error) {
    console.error('Error loading preprocessed data:', error);
    
    // Fallback to monthly consolidated data if available
    try {
      console.log('Trying fallback data source...');
      const fallbackResponse = await fetch('/monthly_consolidated_data.json');
      if (!fallbackResponse.ok) {
        throw new Error(`Unable to load fallback data: ${fallbackResponse.status}`);
      }
      const data = await fallbackResponse.json();
      console.log('Successfully loaded fallback COVID data with', Object.keys(data).length, 'countries');
      return data;
    } catch (secondError) {
      console.error('All loading methods failed:', secondError);
      throw new Error('Unable to load COVID data from any source');
    }
  }
}

// Export a promise that resolves to the data
export const covidDataPromise = loadCovidData();

// For backward compatibility, still export a default
export default {}; 
